<template>
<div class="order" :style="{backgroundColor: orderList.length === 0 ? '#fff' : ''}">
    <van-nav-bar title="订单管理" left-text="返回" left-arrow @click-left="onClickLeft" />
    <div class="tab-list">
        <van-tabs v-model="currentTab" @change="toggleTab">
            <van-tab :title="tab.title" v-for="tab in tabList" :key="tab.status">
                <div class="order-box">
                    <ul>
                        <li class="order-item" v-for="o in orderList" :key="o.oid">
                            <div class="order-item-head">
                                <span class="type">{{o.type}}</span>
                                <span class="address">{{o.oid}}</span>
                                <span class="status" v-if="o.status === 2">已取餐</span>
                                <span class="code" v-else>取餐码：{{o.products[0].id}}</span>
                              
                            </div>
                            <div class="order-item-detail">
                                <div class="item-detail-box" v-for="item in o.products" :key="item.id">
                                    <div class="pay-img">
                                        <img class="img-layout" :src="item.smallImg" alt="">
                                    </div>
                                    <div class="pay-info">
                                        <h3 class="name">{{item.name}}<span class="pay-count">x{{item.count}}</span></h3>
                                        <p class="type">{{item.rule.split('/').join(' / ')}}</p>
                                    </div>
                                    <div class="pay-price">
                                        ￥{{item.price}}
                                    </div>
                                </div>
                            </div>
                            <div class="order-item-total">
                                <span>合计￥{{o.total}}</span>
                            </div>
                            <div class="order-item-footer">
                                <div class="date">{{o.date | formatDate('YYYY-MM-DD hh:mm:ss')}}</div>
                                <button class="detail-btn"  @click="showInfo(o)" v-if="o.type === '外送'">详情</button>
                                <button class="confirm-btn" @click="confirm(o.oid, '/receive')" v-if="o.status === 1">确认收货</button>
                                <button class="remove-btn" @click="confirm(o.oid, '/removeOrder')" v-if="o.status === 2">删除</button>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="empty-box" v-if="orderList.length === 0">
                    <van-empty
                        class="custom-image"
                        image="https://img.yzcdn.cn/vant/custom-empty-image.png"
                        description="订单空空如也，去喝一杯吧~"
                    >
                        <button class="btn" @click="showMenuHandle">去喝一杯 ~</button>
                    </van-empty>
                </div>
            </van-tab>
        </van-tabs>
    </div>

    <van-popup
        v-model="show"
        position="bottom"
        :style="{ height: '30%' }"
        >
        <div class="user">
            <p>订单号：{{currentOrderInfo.oid}}</p>
            <p>收件人：{{currentOrderInfo.receiver}}</p>
            <p>手机号码：{{currentOrderInfo.phone}}</p>
            <p class="address">收货地址：{{currentOrderInfo.address}}</p>
        </div>   
        </van-popup>

</div>
</template>

<script>
import '../assets/scss/order.scss'
import {
    mapGetters
} from 'vuex'
import { utils } from '../assets/js/utils'

export default {
    data() {
        return {
            currentTab: 0,
            orderList: [],
            tabList: [
                {
                    title: '全部订单',
                    status: 0
                },
                {
                    title: '待收货',
                    status: 1
                },
                {
                    title: '已收货',
                    status: 2
                }
            ],
            show: false,
            currentOrderInfo: {}
        }
    },
    created() {
        this.getOrderData(this.currentTab);
    },
    methods: {
        showInfo(o) {

            this.currentOrderInfo = {};
            this.show = true;

            console.log(o.products[0].receiver.split('-')[0]);
            let obj = {
                oid: o.oid,
                receiver: o.products[0].receiver.split('-')[0],
                phone: o.products[0].phone,
                address: o.products[0].address
            }

            this.currentOrderInfo = obj;
        },
        //
        showMenuHandle() {
            this.$router.push({name: 'Menu'})
        },
        // 确认收货与删除订单
        confirm(oid,url) {
            if (!this.getToken) {
                this.$router.push({
                    name: 'Login'
                })

                return;
            }

            let data = utils.queryString({
                appkey: this.appkey,
                tokenString: this.getToken,
                oid
            })

            this.$toast({
                type: 'loading',
                message: '加载中',
                forbidClick: true,
                loadingType: 'spinner',
                duration: 0
            });

            this.axios({
                    method: 'POST',
                    url,
                    data
                })
                .then(res => {
                    this.$toast.clear();
                    this.$toast(res.data.msg)
                    console.log(res);
                    if(res.data.code === 80000 || res.data.code === 90000) {
                        this.getOrderData(this.currentTab)    
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                    console.log(err);

                })
        },
        // 切换标签
        toggleTab(name) {
            this.getOrderData(name)
        },
        // 获取全部订单
        getOrderData(status) {
            if (!this.getToken) {
                this.$router.push({
                    name: 'Login'
                })

                return;
            }

            let params = {
                appkey: this.appkey,
                tokenString: this.getToken,
                status
            }

            this.$toast({
                type: 'loading',
                message: '加载中',
                forbidClick: true,
                loadingType: 'spinner',
                duration: 0
            });

            this.axios({
                    method: 'GET',
                    url: '/findOrder',
                    params
                })
                .then(res => {
                    this.orderList = [];
                    this.$toast.clear();
                    console.log(res);
                    if(res.data.code === 70000) {
                        let obj = {};
                        res.data.result.forEach(item => {
                            
                            if(!obj[item.oid]) {

                                obj[item.oid] = {
                                    oid: item.oid,
                                    date: item.createdAt,
                                    total: item.count * item.price,
                                    products: [item],
                                    status: item.status,
                                    type: item.receiver.split('-')[1]
                                }
                            }else {
                                obj[item.oid].total += item.count * item.price,
                                obj[item.oid].products.push(item)
                            }

                        });

                        for(let key in obj) {
                            this.orderList.push(obj[key])
                        }


                        this.orderList.sort(function(a,b) {
                            return new Date(b.date).getTime() - new Date(a.date).getTime();
                        })

                        console.log(this.orderList);
                        
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                    console.log(err);

                })

        },
        onClickLeft() {
            this.$router.go(-1);
        }
    },
    computed: {
        ...mapGetters(['getToken'])
    }
}
</script>
